fetch("json/data.json").then( (response) =>
		response.json()
	).then( (products) => {

	//performSearch(1, getId("productsBox"), 4, 0, 1600);
	performSearch(1, productsBox, 4, 0, 1600);
	//performSearch(2, getId("productsHit"), 6, 300, 600);
	performSearch(2, productsHit, 6, 300, 600);

  pg.performSearch = performSearch;

  /* (countItem*num), $(container), countItem, min price, max price*/
	function performSearch(number, box, countItem, minPrice, maxPrice) {
		const page = typeof number === "number" ? number || 1 : 1;
		//const { value } = getId("search");
		const value = '';//getId("list-complete").value;
		//console.log( 'value: '+ getId("list-complete").value )

		function comparator(order) {
			return [
				(a, b) => (a.product > b.product ? 1 : -1),
				(a, b) => (Number(a.price) > Number(b.price) ? 1 : -1),
				(a, b) => (Number(a.price) < Number(b.price) ? 1 : -1)
			][order];
		}

		const [min, max] = ((a, b) => (a > b ? [b, a] : [a, b]))(
			//0, 2000
			minPrice, maxPrice
			//Number(getId("price-min").value),
			//Number(getId("price-max").value)
		);

		//getId("range-display").childNodes[0].textContent = `Price (${min}...${max})`;

		const result = fnSearch(
			products,
			value,
			''
			//document.getElementById("exact-search").checked
		).filter(({ price }) => {
			price = Number(price);
			return min <= price && price <= max;
		});

		//result.sort(comparator(document.getElementById("order").value));
		result.sort(comparator(0));// choose->0or1or2

		let html = result
			.map(({ image, product, price }, index) => {
				if (
					index >=
					page * countItem // //document.getElementById("results-per-page").value
				)
					return ``;

				const [, integer, decimal] = price.match(/^(\d+\.?\-?)(\d*)/);
				return `<div class="col-12 col-sm-6 col-md-4 col-lg-3 col-xl-2 text-center">
						<a href="/item.html" class="article_link" data-model="${product}">
							<img src="${image}">
							<p>${product}</p>
							<span class="price">${integer}${decimal ? "" : "-"}<span class="cents">${
					decimal ? (decimal + "0").slice(0, 2) : ""
				}</span></span>
						</a>
					</div>
					`;
			})
			.join("");

		if (
			result.length >
			page * countItem //6 //document.getElementById("results-per-page").value
		)
			html += `
					<button type="button" id="btn-moreitem" class="btn btn-success" onclick="pg.performSearch(${page + 1})">Load more</button>
				`;

		box.innerHTML = html;
	}
});

